import { Suspense } from 'react';
import { Canvas} from '@react-three/fiber';
import { useGLTF, Stage,  OrbitControls, Preload, Lightformer, Environment, Effects, ContactShadows } from '@react-three/drei'
import { EffectComposer, Bloom } from '@react-three/postprocessing';
import CanvasLoader from './CanvasLoader';

 function App() {

  return (
    <div className='w-full h-full canvas-div'>
    <Canvas 
        gl={{ logarithmicDepthBuffer: true, antialias: false }}
        frameloop='demand'
        shadows 
        camera={{ position: [-15, 0, 10], fov: 25 }}>
        <Suspense 
              fallback={<CanvasLoader />}
        > 
            <Stage intensity={0.1} environment="city" shadows={{ type: 'accumulative', bias: -0.001 }} adjustCamera={false}>
              <Scene />
            </Stage>
            <OrbitControls autoRotate autoRotateSpeed={0.05} 
                makeDefault 
            />
            
            <EffectComposer disableNormalPass>
                <Bloom luminanceThreshold={1} mipmapBlur />
            </EffectComposer>
            <Preload all />
        </Suspense> 
        </Canvas>
    </div>
  )
}

function Scene() {
  const { scene } = useGLTF(`${process.env.PUBLIC_URL}/models/3.glb`)
  return (<primitive
        receiveShadow
        object={scene}
        scale={0.04} 
        position={[0, -3, 0]}
        rotation={[0, 1.2, 0]}
    />
  )
}


useGLTF.preload(`${process.env.PUBLIC_URL}/models/3.glb`);

export default App
