import React, { Suspense, useRef} from 'react'
import { Canvas, useFrame, useLoader, useThree } from '@react-three/fiber';
import { Effects, OrbitControls, Preload, Stage, useFBO, useGLTF } from '@react-three/drei';
import { OBJLoader } from "three/examples/jsm/loaders/OBJLoader";
import { MTLLoader } from "three/examples/jsm/loaders/MTLLoader";
import { AdditiveBlendingShader } from './shaders/AdditiveBlendingShader'
import { VolumetricLightShader } from './shaders/VolumetricLightShader'
import { FXAAShader } from 'three-stdlib';
import CanvasLoader from "./CanvasLoader"

function Post() {

  const { gl, size } = useThree()
  const occlusionRenderTarget = useFBO()
  const occlusionComposer = useRef()
  const composer = useRef()
  useFrame(() => {
    occlusionComposer.current.render()
    composer.current.render()
  }, 1)
  return (
    <>
      <Effects ref={occlusionComposer} disableGamma disableRender args={[gl, occlusionRenderTarget]} renderToScreen={false}>
          <shaderPass args={[VolumetricLightShader]} needsSwap={false} />
        </Effects>
        <Effects ref={composer} disableRender>
          <shaderPass args={[AdditiveBlendingShader]} uniforms-tAdd-value={occlusionRenderTarget.texture} />
          <shaderPass args={[FXAAShader]} uniforms-resolution-value={[1 / size.width, 1 / size.height]} renderToScreen />
        </Effects>
    </>
  )
}
const Scene = () => {
    // const materials = useLoader(MTLLoader, `${process.env.PUBLIC_URL}/models/az4.mtl`);
    // const azumi = useLoader(OBJLoader, `${process.env.PUBLIC_URL}/models/az4.obj`, (loader) => {
    //     materials.preload();
    //     loader.setMaterials(materials);
    // });
    const { scene } = useGLTF(`${process.env.PUBLIC_URL}/models/3.glb`);

    return <Suspense>
      <primitive
      receiveShadow
          object={scene}
          scale={0.04} 
          position={[0, -3, 0]}
          rotation={[0, 1.2, 0]}
        />;
      </Suspense>
};


const Azumi = () => {


    return (
        <div className='w-full h-full canvas-div'>
          <Canvas
           frameloop='demand'
           shadows
           dpr={[1, 2]}
           camera={{ position: [20, 6, 5], fov: 25 }}
           gl={{ preserveDrawingBuffer: true, antialias: false }}
          >
            <Suspense 
              fallback={<CanvasLoader />}
            >
              <spotLight intensity={0.1} penumbra={1} position={[350, -150, 0]} castShadow />
              <Stage intensity={0.1} environment="city" shadows={{ type: 'accumulative', bias: -0.001 }} adjustCamera={false}>
                <Scene />
              </Stage>
              <OrbitControls />
                <Post />
              
            </Suspense>

            <Preload all />
        </Canvas>  
    </div>
    )
}

export default Azumi
